import { Entity } from '@backstage/catalog-model';
import { useEntity } from '@backstage/plugin-catalog-react';
import { Chip, Grid, Link } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import React, { CSSProperties } from 'react';
import { useAsync } from 'react-use';

import {
  EmptyState,
  InfoCard,
  InfoCardVariants,
  MarkdownContent,
  Progress,
  Table,
  TableColumn,
} from '@backstage/core-components';
import { WarningPanel } from '@backstage/core-components';
import { configApiRef, useApi } from '@backstage/core-plugin-api';
import { SystemScore } from '../../types/SystemScore';
import { scoreToColorConverter } from '../../helpers/scoreToColorConverter';
import { SystemScoreEntry } from '../../types/SystemScoreEntry';

//this is an interface used for table entries. We need to enrich the original SystemScoreEntry with the "area" group, see bellow allEntries reduce
interface SystemScoreTableEntry extends SystemScoreEntry {
  area: string;
}

//lets prepare some styles
const useStyles = makeStyles(theme => ({
  badgeLabel: {
    color: theme.palette.common.white,
  },
  header: {
    padding: theme.spacing(2, 2, 2, 2.5),
  },
  action: {
    margin: 0,
  },
  disabled: {
    backgroundColor: theme.palette.background.default,
  },
}));

export const ScoreCard = ({
  variant = 'gridItem',
}: {
  entity?: Entity;
  variant?: InfoCardVariants;
}) => {
  const { entity } = useEntity();
  const systemName = entity.metadata.name;

  //let's load the entity data from url defined in config
  const configApi = useApi(configApiRef);
  const jsonDataUrl = configApi.getOptionalString('scorecards.jsonDataUrl') ?? 'https://unknown-url-please-configure';
  const {
    loading: loading,
    error: error,
    value: value,
  } = useAsync(async () => {
    const urlWithData = `${jsonDataUrl}${systemName}.json`;
    //DEBUG: console.log(`Fetching data from ${urlWithData}`);
    const result: SystemScore = await fetch(urlWithData).then(res => {
      switch (res.status) {
        case 404:
          return null;
        case 200:
          return res.json();
        default:
          throw `error from server (code ${res.status})`;
      }
    });
    return result;
  });


  const classes = useStyles();

  //let's prepare the "chip" used in the up-right card corner
  let gateLabel = 'Not computed';
  let gateStyle = {
    margin: 0,
    backgroundColor: scoreToColorConverter(value?.scoreSuccess)
  }
  if (value?.scorePercent || value?.scorePercent === 0) {
    gateLabel = `Total score: ${value?.scorePercent} %`;
  }
  const qualityBadge = !loading && (
    <Chip
      label={gateLabel}
      style={gateStyle}
    />
  );

  //let's define the main table columns
  const columns: TableColumn<SystemScoreTableEntry>[] = [
    {
      title: 'Area',
      field: 'area',
      grouping: true,
      groupTitle: 'Area',
      defaultGroupOrder: 0, // area column is the one we will group by default. It cant be changed (Groupbar is overriden to null)
      width: '1px', //we wont show the groupbar so we dont want to see this column either
      render: (data, type) => {
        if (type == 'group') {
          //we need to find the area..it's based on title (see allEntries reduce below) as we can used this for ordering too
          const area = value?.areaScores.find(area => area.title == data.toString());
          const areaGateStyle = {
            margin: 0,
            backgroundColor: scoreToColorConverter(area?.scoreSuccess)
          }
          const areaGateLabel = `${data} ${area?.scorePercent} %`;
          return (<Chip label={areaGateLabel} style={areaGateStyle} />);
        }
        return (<Link>{data.area}</Link>);
      }
    },
    {
      title: 'Requirement',
      field: 'title',
      grouping: false,
      render: (systemScoreEntry) => (
        <span>
          <Link href={`https://dev.azure.com/oriflame/GlobalDev/_wiki/wikis/GlobalDev.wiki/${systemScoreEntry.id}`} target='_blank'>{systemScoreEntry.title}</Link>
          {systemScoreEntry.isOptional ? ' (Optional)' : null}
        </span>
      )
    },
    {
      title: 'Score',
      field: 'scorePercent',
      grouping: false
    },
    {
      title: 'Details',
      field: 'details',
      grouping: false,
      render: (systemScoreEntry) => (
        <MarkdownContent content={systemScoreEntry.details} />
      )
    },
  ];

  //like this we can change the style of the whole row based on success or not
  const rowStyleFunc = (rowData: SystemScoreTableEntry): CSSProperties => ({
    color: scoreToColorConverter(rowData?.scoreSuccess)
  })

  /*
  here comes the tricky part. We have json like this:
  {
    ...
    areaScores: [
      {
        id: 1,
        title: "documentation",
        ...
        scoreEntries : [ 
          {
            id: 222,
            title: "readme"
          },
          {
            id: 333,
            title: "RFP"
          },
          ... other score entries
        ]
      },
      ... other areas with other score entries
    ] 
  }
  and we want to have a flat array of score entries for table grouped by area, e.g.
  [
    {
      id: 222,
      title: "readme",
      area: "documentation",
      ...
    },
    {
      id: 333,
      title: "RFP",
      area: "documentation",
      ...
    },
    ...
  ]
  so we want to basically go through all areaScores and get all its entries to one big array with a new 
  property "area" pointing back to the area so we can later on get the area score... 
  */
  let allEntries: SystemScoreTableEntry[] = [];
  if (value && value.areaScores.length > 0) {
    allEntries = value.areaScores.reduce<SystemScoreTableEntry[]>(
      (entries, area) => entries.concat(
        area.scoreEntries.map(entry => {
          return {
            area: area.title,
            ...entry
          };
        }
        )
      ), []);
  }

  return (
    <InfoCard
      title={`Score Card for ${systemName}`}
      variant={variant}
      headerProps={{
        action: qualityBadge,
        classes: {
          root: classes.header,
          action: classes.action,
        },
      }}
    >
      {loading && <Progress />}

      {error && <WarningPanel severity="error" title="Could not load data.">
        {error.name} {error.message}
      </WarningPanel>
      }

      {!loading && !value && (
        <EmptyState
          missing="info"
          title="No information to display"
          description={`There is no data available for '${systemName}'.`}
        />
      )}

      {!loading && value && (
        <>
          <Grid
            item
            container
            direction="column"
            justify="space-between"
            alignItems="stretch"
            style={{ height: '100%' }}
            spacing={0}
          >
            <Table<SystemScoreTableEntry>
              title="Score of each requirement"
              options={{ search: true, paging: false, rowStyle: rowStyleFunc, grouping: true }}
              columns={columns}
              data={allEntries}
              components={{
                Groupbar: () => null //we do not want to display possibility to change grouping
              }}
            />
          </Grid>
        </>
      )}
    </InfoCard>
  );
};